*{
    font-family:'Pacifico', cursive;
}

Table{
    width: 1250px;
    font-size: 20px;
   
}

.table-hover thead :hover{
    color: lightcoral;
    font-size: 40px;
}

.table-hover tbody tr{
    background-color: rgb(250, 177, 177);
}




@media (max-width:700px) {
    .tb{
        padding: 0px;
        background-color: none;
    }
    .table-hover thead :hover{
        color: lightcoral;
        font-size: 20px;
    }
}


